
.ft-align-left{flex-direction:row;text-align:left
}
.ft-align-center{flex-direction:row;justify-content:center;text-align:center
}
.ft-align-right{flex-direction:row-reverse;text-align:right
}
.ft-spinner{width:64px;height:64px;animation:ft-spinner 1s infinite linear;border-right:10px solid #2196F3;border-bottom:10px solid rgba(33,150,243,0.2);border-left:10px solid rgba(33,150,243,0.2);border-top:10px solid rgba(33,150,243,0.2);border-radius:50%
}
@keyframes ft-spinner{
0%{transform:rotate(90deg)
}
100%{transform:rotate(450deg)
}
}
.flextable{border:1px solid rgba(0,0,0,0.12);box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.2),0 1px 5px 0 rgba(0,0,0,0.12);background:#fff
}
.ft-no-data{padding:20px 16px;width:100%;text-align:center;font-size:2.0em
}
.ft-loader{display:flex;justify-content:center;align-items:center;padding:20px
}
.ft-header{padding:20px 16px;display:flex;justify-content:space-between
}
.ft-header-title{color:rgba(0,0,0,0.5);font-size:1.6rem;font-weight:bold
}
.ft-footer{padding:20px 16px;display:flex;justify-content:flex-end
}
.ft-grid{display:flex;flex-direction:column
}
.ft-row{display:flex;align-items:center;border-top:1px solid rgba(0,0,0,0.15)
}
.ft-row-selected{background:#ccc
}
.ft-cell{width:100%;overflow:hidden;padding:16px
}
.ft-heading-row{display:flex;width:100%
}
.ft-heading-cell{overflow:hidden;display:flex;width:100%;padding:16px;color:rgba(0,0,0,0.5);font-weight:bold;user-select:none;-webkit-user-select:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none
}
.ft-heading-cell-sorted{color:#000
}
.ft-clickable{cursor:pointer
}
.ft-accordion{padding:20px 16px;width:100%;height:30px;position:absolute;cursor:pointer;opacity:0
}
.ft-accordion+.ft-accordion-title+.ft-accordion-panel{transition:all 300ms ease-in-out;padding:0px 16px;max-height:0;overflow:hidden;opacity:0
}
.ft-accordion:checked+.ft-accordion-title+.ft-accordion-panel{opacity:1;max-height:300px;padding:20px 16px
}



